@media screen and (max-width: 450px) {
    .list-data-container {
        .list-details {
            flex-basis: 95%;

            .add-form {
                .item-form-container {
                    .item-form {
                        width: 100%;
                        flex-direction: column;
                        .item-label {
                            width: 95%;
                        }
                    }
                }
            }
        }

    }
}

@media screen and (max-width: 600px) and (min-width: 451px) {
    .list-data-container {
        .list-details {
            flex-basis: 95%;

            .add-form {
                .item-form-container {
                    .item-form {
                        width: 100%;
                    }
                }
            }
        }

    }
}

@media screen and (max-width: 1024px) and (min-width: 601px) {
    .list-data-container {
        .list-details {
            flex-basis: 80%;
        }
    }
}