@import "index";

.signup-container {
    width: 40%;
    height: auto;
    padding-bottom: 2rem;
    display: flex;
    justify-content: center;
    color: $brown-sugar;

    .signup-form {
        width: 100%;
        height: 100%;
        padding: 2rem 0;
        margin: 2rem 0 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border: 1.1rem solid rgb(253, 228, 216);
        box-shadow: 1px 1px 5px $cafe-au-lait;
        border-radius: $border;

        .h2-signup {
            margin: 1rem 0;
            letter-spacing: 0.1rem;
            text-align: center;
        }
        .h5-signup {
            width: 90%;
            padding: 0.3rem 2rem;
            font-weight: 100;
        }

        .signup-label {
            width: 80%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding: 0.3rem;
            font-weight: 700;

            .signup-input {
                height: 2.5rem;
                width: 100%;
                padding: 1rem;
                margin: 0.3rem 0;
                border-radius: $border;
                border: 1px solid $cafe-au-lait;
                font-size: 1.1rem;
                color: $brown-sugar;

                &::placeholder {
                    font-family: 'El Messiri', sans-serif;
                }
            }

            .signup-input:focus {
                outline: none;
                border: 1px solid $coffee-darker;
            }
        }
        
        .sign-btn {
            cursor: pointer;
            font-size: 1.2rem;
            font-weight: bold;
            padding: 0.7rem 2.2rem;
            color: $champagne-darker;
            background-color: $cafe-au-lait;
            border-radius: 0.18rem;
            border: 0.14rem solid rgba(157, 107, 83, 0.6);
            border-right-color: rgba(113, 69, 51, 0.7);
            border-bottom-color: rgba(113, 69, 51, 0.7);
        }
        .sign-btn:hover {
            color: $tumbleweed;
        }

        .already-registered {
            font-size: 0.8rem;
            margin-top: 0.5rem;
            text-decoration: underline;
        }
    }
}


@media screen and (max-width: 599px) {
    .signup-container {
        width: 95%;
        padding: 1rem 0;
        .signup-form {
            margin: 0 0 2rem;
            padding: 0.5rem 0;
            .signup-label {
                width: 100%;
            }
        }
    }
}

@media screen and (max-width: 768px) and (min-width: 600px) {
    .signup-container {
        width: 75%;
    }
}

@media screen and (max-width: 949px) and (min-width: 769px) {
    .signup-container {
        width: 70%;
    }
}

@media screen and (max-width: 1300px) and (min-width: 950px) {
    .signup-container {
        width: 55%;
    }
}